const { registerBlockType } = wp.blocks;
const { RichText } = wp.blockEditor;

registerBlockType( 'schoolbug/aos-block', {
    title: 'AOS Block',
    icon: 'editor-alignleft',
    category: 'common',
    attributes: {
        content: {
            type: 'string',
            source: 'html',
            selector: 'p',
        },
        aos: {
            type: 'string',
            default: 'fade-up', // Default animation effect
        },
    },
    edit: ( props ) => {
        const { attributes: { content, aos }, setAttributes } = props;

        return (
            <div>
                <RichText
                    tagName="p"
                    className="aos-block"
                    value={ content }
                    onChange={ ( value ) => setAttributes( { content: value } ) }
                    placeholder="Enter block content..."
                />
                <div>
                    <label>
                        Animation Type:
                        <select
                            value={ aos }
                            onChange={ ( e ) => setAttributes( { aos: e.target.value } ) }
                        >
                            <option value="fade-up">Fade Up</option>
                            <option value="fade-down">Fade Down</option>
                            <option value="fade-left">Fade Left</option>
                            <option value="fade-right">Fade Right</option>
                            <option value="flip-left">Flip Left</option>
                            <option value="flip-right">Flip Right</option>
                            {/* Add more AOS animation types here */}
                        </select>
                    </label>
                </div>
            </div>
        );
    },
    save: ( props ) => {
        const { attributes: { content, aos } } = props;

        return (
            <p className={ `aos-block` } data-aos={ aos }>
                { content }
            </p>
        );
    },
} );
